.overlay {
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.4);
    overflow: hidden;
    width: 100%;
    height: 0;
    -webkit-transition: .3s ease;
    -o-transition: .3s ease;
    transition: .3s ease;
}

.grid {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
}

.container {
    position: relative;
    margin: 4px;
    width: 20vw;
    height: 20vw;
    min-width: 180px;
    min-height: 180px;
    max-width: 225px;
    max-height: 225px;
}

.container:hover .overlay {
    height: 40px;
}

.picture {
    width: auto;
    height: 100%;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
}



