body {
  margin: 0;
  padding: 0;
  background: #ecf0f1;
  font-family: 'Roboto', 'Avenir', Helvetica, Arial, sans-serif;
  line-height: 1.5;
}

@media (min-width: 768px) {
    .topNavContainer {
        position: -webkit-sticky;
        position: sticky;
        top: 0;
    }
}

.topNavAccent {
    background-color: #f1c40f;
}



